.custom-modal{
  .modal-content {
    border-radius: 10px;
    overflow: hidden;
    border: none;
    .modal-header {
      padding: 15px 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: $primary;
      .modal-title {
        color: $white;
        font-family: $font-bold;
        font-size: 26px;
      }
    }
    .modal-body {
      padding: 40px;
      .form-group {
        margin-bottom: 30px;
        &.show-message {
          .valid-feedback {
            display: block;
            color: #212529;
          }
        }
        .form-label {
          font-size: 14px;
          font-family: $font-semiBold;
        }
        input, select {
          outline: none;
          box-shadow: none;
          border: 1px solid #e7e7e7;
          font-family: $font-regular;
          color: $black;
          border-radius: 5px;
          font-size: 14px;
          height: 50px;
          padding: 5px 20px;
          box-shadow: 0 5px 9px 0 rgba(0, 0, 0, 0.11);
          &::placeholder {
            color: #858585;
            text-transform: capitalize;
          }
        }
      }
      .modal-button {
        display: flex;
        justify-content: center;
        align-items: center;
        button {
          //padding: 15px 40px;
        }
      }
    }
  }
}