.dark-theme {
  .contain-container{
    background: #0e0e10;
  }
  .about-page {
    .about-us {
      h1 {
        color: $b-black;
      }
      p {
        color: $b-black;
      }
      .about-nav {
        li {
          a {
            background-color: $w-white;
            color: $b-black;
            &.active {
              background-color: $p-primary;
              color: $b-black;
            }
          }
        }
      }
    }
    .card-section {
    }
    h1 {
      color: $b-black;
    }
    h2 {
      color: $b-black;
    }
    p {
      color: $b-black;
    }
  }
  .channel-page {
    .inner-container {
      .channel-page-header {
        .user-detail {
          border: 1px solid $b-black;
          h4 {
            color: $b-black;
          }
          .tag {
            background: $p-primary;
            color: $b-black;
          }
        }
        ul {
          margin: 0 0 15px 20px;
          li {
            .users {
              svg {
                color: $p-primary;
              }
              span {
                color: $p-primary;
              }
            }
            button {
              background: $w-white;
              border: 1px solid $b-black;
              color: $b-black;
              &:hover {
                background: $p-primary;
                color: $b-black;;
                border-color: $p-primary;
              }
            }
          }
        }

      }
      .channel-detail-container {
        background: $w-whisper;
        .channel-detail-header {
          h4 {
            color: $b-black;
          }
          .users-and-viewers {
            .icon-with-text {
              svg {
                color: $p-primary;
              }
              span {
                color: $p-primary;
              }
              &:last-child {
                svg {
                  color: $b-black;
                }
              }
            }
          }
        }
        .channel-detail-body {
          .channel-detail {
            .channel-heading-with-text {
              h4 {
                color: $b-black;
              }
              p {
                color: $p-primary;
              }
            }
          }
        }
      }
    }
    .stream-chat-container {
      background: $l-lightenGray;
      .stream-chat-header {
        .chat-icon {
          color: $b-black;
        }
        border-bottom: 1px solid #d9d6d6;
      }
      .stream-chat-body {
        .chat-container {
          background: $w-white;
          li {
            p {
              span {
                color: $p-primary;
              }
              color: $b-black;
            }
          }
          &::-webkit-scrollbar-track {
            background: #cccccc;
          }
          &::-webkit-scrollbar-thumb {
            background: #6f6f6f;
          }
          &::-webkit-scrollbar-thumb:hover {
            background: #6f6f6f;
          }
        }
        a {
          color: $p-primary;
        }
      }
      .stream-chat-footer {
        background: $w-whisper;
        input {
          color: $b-black;
          &::placeholder {
            color: $b-black;
          }
        }
        button {
          svg {
            color: $b-black;
          }
        }
      }
    }
    .tab-section {
      .tab-section-title {
        color: $p-primary;
      }
      &.follower-section {
        .videos {
          .video {
            background: $w-white;
          }
        }
      }
      .empty {
        p {
          color: $b-black;
        }
      }
    }
  }
  .footer {
    background: $p-primary;
    p {
      color: $b-black;
    }
  }
  .normal-button {
    background: $p-primary;
    color: $b-black;
    border-color: $p-primary;
    outline-color: $p-primary;
    &:focus{
      background: $p-primary !important;
      border-color: $p-primary !important;
    }
    &:active{
      background: $p-primary !important;
      border-color: $p-primary !important;
    }
    &:hover{
      background: $p-primary !important;
      border-color: $p-primary !important;
    }
  }
  .black-button {
    background: $w-white !important;
    color: $b-black !important;
    border-color: $b-black !important;
    &:focus{
      background: $w-white !important;
      border-color: $b-black !important;
    }
    &:active{
      background: $w-white !important;
      border-color: $b-black !important;
    }
    &:hover{
      background: $w-white !important;
      border-color: $b-black !important;
    }
  }
  .heading {
    color: $b-black;
    span {
      color: $p-primary;
    }
  }
  .c-card {
    .c-card-header {
      .tag {
        background: $p-primary;
        color: $w-white;
      }
    }
    .c-card-body {
      background: $w-whisper;
      .c-card-body-content {
        h5 {
          color: $b-black;
        }
        p {
          color: $b-black;
        }
      }
    }
  }
  .circle-button {
    //background: $p-primary;
    background: $w-whisper;
    svg {
      color: $p-primary;
    }
  }
  .page-title {
    color: $b-black;
  }
  .tab-nav {
    li {
      a {
        color: $b-black;
        &.active {
          color: $p-primary;
          border-bottom-color: $p-primary;
        }
      }
    }
  }
  .header {
    .navbar {
      background: $o-offWhite;
      .navbar-nav{
        .nav-link {
          color: $b-black !important;
          svg {}
        }
        .dropdown {
          &.select-language {
            svg {
              color: $b-black;
            }
            .down-arrow {}
            select {
              background: transparent;
              color: $b-black;
              option {
                color: $w-white;
              }
            }
          }
          .dropdown-toggle {
            background: $w-whisper;
          }
          .dropdown-menu {
            background: $w-white;
            a {
              color: $b-black;
              svg {
                color: $b-black;
              }
              .custom-control-input:checked~.custom-control-label::before {
                border-color: $p-primary;
                background-color: $p-primary;
              }
              &:hover {
                color: $p-primary;
                svg {
                  color: $p-primary;
                }
              }
            }
          }
        }
      }
      .header-search-container{
        input {
          color: $b-black;
          background:  $w-white;
          &::placeholder{
            color: $b-black;
            color: $b-black;
          }
        }
        button{
          background: $w-whisper;
          svg{
            color: $b-black;
          }
        }
      }
    }
  }
  .page {
    .accordion {
      .accordion-container {
        .accordion-header {
          background: $p-primary;
          button {
            background: transparent;
            color: $b-black;
            svg {
            }
          }
        }
        .accordion-body {
          background: $l-lightenGray;
          p {
            color: $b-black;
          }
        }
      }
    }
  }
  .sidebar {
    background: $l-lightenGray;
    .channels-container {
      .channels-list {
        &::-webkit-scrollbar-track {
          background: #cccccc;
        }
        &::-webkit-scrollbar-thumb {
          background: #6f6f6f;
        }
        &::-webkit-scrollbar-thumb:hover {
          background: #6f6f6f;
        }
        .channels-list-item {
          a {
            border-bottom: 1px solid #d8d6d7;
            &.active {
              background: $w-white;
              border-left-color: $p-primary;
            }
            &:hover {
              background: $w-white;
              border-left-color: $p-primary;
            }
            .channels-list-item-content {
              h6 {
                color: $b-black;
              }
              p {
                color: $g-gray;
              }
            }
            .status {
              background: $p-primary;
            }
            .viewers-text {
              color: $g-gray;
            }
          }
        }
      }
    }
  }
  .settings {
    .tab-section {
      .tab-section-title {
        color: $p-primary;
      }
      .tab-section-description {
        color: $b-black;
      }
      .tab-section-body {
        background: $o-offWhite;
        .form-group {
          .form-label {
            color: $b-black;
          }
          .input-with-text {
            p {
              color: #6f6f6f;
            }
            input {
              color: $b-black;
              background: $w-white;
              &:focus {
                border-color: #f12b2d;
              }
            }
            textarea {
              color: $b-black;
              background: $w-white;
              &:focus {
                border-color: #f12b2d;
              }
            }
            p {}
          }
          &.upload-picture {
            align-items: center;
            margin-bottom: 35px;
            .input-with-text {
              padding-left: 25px;
              color: #6f6f6f;
            }
            .upload-picture-button {
              label {
                background: $p-primary;
                color: $b-black;
              }
            }
          }
        }
      }
    }
  }
  .privacy-section{
    p{
      color: $b-black;
    }
  }
  .custom-modal{
    .modal-content {
      background-color: $w-whisper ;
      .modal-header {
        background: $p-primary;
        .modal-title {
          color: $w-white;
        }
      }
      .modal-body {
        .form-group {
          input {
            border: 1px solid #e7e7e7;
            color: $b-black;
            background: $w-whisper;
            &::placeholder {
              color: #858585;
            }
          }
        }
      }
    }
  }
  .main-wrapper {
    .categories-section {
      background: $p-primary;
      .heading {
        color: $b-black;
        span {
          color: $w-white;
        }
      }
    }
  }
}