.channel-page {
  display: flex;
  flex-direction: row;
  height: 100%;
  &.chat-close {
    .inner-container {
      width: calc(100% - 74px);
      transition: 0.3s;
    }
  }
  .inner-container {
    flex: 1;
    width: calc(100% - 300px);
    transition: 0.3s;
    display: block;
    .channel-page-header {
      margin: 25px 0 10px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: center;
      .user-detail {
        display: flex;
        align-items: center;
        border: 1px solid $black;
        padding: 5px 20px 5px 12px;
        border-radius: 100px;
        margin-bottom: 15px;
        //flex: 1;
        img {
          width: 58px;
          height: 58px;
          border-radius: 58px;
        }
        h4 {
          margin: 0 35px 0 20px;
          color: $black;
          font-family: $font-bold;
          font-size: 14px;
          flex: 1;
        }
        .tag {
          background: $primary;
          padding: 4px 30px;
          color: $white;
          font-size: 12px;
          font-family: $font-bold;
          border-radius: 100px;
        }
      }
      ul {
        margin: 0 0 15px 20px;
        li {
          .users {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            margin-bottom: 3px;
            svg {
              font-size: 16px;
              color: $primary;
              margin-right: 10px;
            }
            span {
              font-size: 14px;
              color: $primary;
              font-family: $font-bold;
            }
          }
          button {
            background: $white;
            font-size: 14px;
            font-family: $font-bold;
            outline: none;
            padding: 18px 30px;
            border: 1px solid $black;
            border-radius: 100px;
            transition: 0.3s;
            height: 55px;
            &:hover {
              background: $primary;
              color: $white;
              border-color: $primary;
            }
          }
        }
      }

    }
    .video-container {
      margin: 5px 0 20px;
      width: 100%;
      height: 500px;
      border-radius: 10px;
      overflow: hidden;
      box-shadow: 0 5px 21px 0 rgba(0, 0, 0, 0.21);
      iframe {}
    }
    .channel-detail-container {
      background: $whisper;
      padding: 25px 20px;
      box-shadow: 0 5px 21px 0 rgba(0, 0, 0, 0.21);
      .channel-detail-header {
        display: flex;
        flex-direction: row;
        margin-bottom: 30px;
        h4 {
          font-family: $font-bold;
          color: $black;
          font-size: 20px;
          flex: 1;
          margin: 0;
          text-transform: uppercase;
        }
        .users-and-viewers {
          display: flex;
          margin-left: 50px;
          .icon-with-text {
            margin-right: 10px;
            svg {
              color: $primary;
              font-size: 16px;
              margin-right: 8px;
            }
            span {
              font-size: 14px;
              color: $primary;
              font-family: $font-bold;
            }
            &:last-child {
              margin-right: 0;
              svg {
                color: $black;
              }
            }
          }
        }
      }
      .channel-detail-body {
        display: flex;
        flex-direction: row;
        align-items: center;
        img {
          margin-right: 25px;
          width: 165px;
          height: 115px;
        }
        .channel-detail {
          .channel-heading-with-text {
            margin-bottom: 10px;
            h4 {
              margin: 0;
              color: $black;
              font-family: $font-bold;
              font-size: 14px;
              text-transform: uppercase;
            }
            p {
              margin: 0;
              color: $primary;
              font-family: $font-bold;
              font-size: 14px;
              text-transform: uppercase;
            }
            &:last-child {
              margin-bottom: 10px;
            }
          }
        }
      }

    }
    .section {
      .slick-slider{
        margin-bottom: 30px;
      }
    }
  }
  .stream-chat-container {
    width: 300px;
    padding: 20px 15px;
    background: $lightenGray;
    .stream-chat-header {
      display: flex;
      align-items: center;
      .heading {
        font-size: 14px;
        flex: 1;
        margin: 0 15px;
        text-align: center;
      }
      .chat-icon {
        font-size: 26px;
        color: $black;
      }
      border-bottom: 1px solid #d9d6d6;
      margin: 0 -15px;
      padding: 0 15px 20px;
    }
    .stream-chat-body {
      .chat-container {
        background: $white;
        padding: 15px 20px;
        margin: 10px 0;
        list-style: none;
        height: 400px;
        overflow-y: auto;
        li {
          display: flex;
          flex-direction: row;
          align-items: center;
          margin-bottom: 5px;
          img {
            border-radius: 100px;
            width: 32px;
            height: 32px;
            margin-right: 15px;
            object-fit: cover;
          }
          p {
            span {
              font-family: $font-bold;
              color: $primary;
            }
            font-family: $font-medium;
            color: $black;
            font-size: 12px;
            margin: 0;
          }
        }
        &::-webkit-scrollbar {
          width: 10px;
          border-radius: 0;
        }
        &::-webkit-scrollbar-track {
          background: #cccccc;
          border-radius: 0;
        }
        &::-webkit-scrollbar-thumb {
          background: #6f6f6f;
          border-radius: 0;
        }
        &::-webkit-scrollbar-thumb:hover {
          background: #6f6f6f;
        }
      }
      a {
        text-decoration: none;
        color: $primary;
        font-size: 14px;
        font-family: $font-bold;
        margin-bottom: 10px;
        padding: 0 10px;
        display: flex;
      }
    }
    .stream-chat-footer {
      margin: 0 -15px;
      padding: 0 15px;
      background: #cdcdcd;
      display: flex;
      align-items: center;
      input {
        flex: 1;
        font-size: 14px;
        color: $black;
        font-family: $font-medium;
        height: 47px;
        padding: 0 5px;
        background: transparent;
        border: none;
        outline: none ;
        &::placeholder {
          color: $black;
        }
      }
      button {
        background: transparent;
        border: none;
        padding: 0;
        svg {
          font-size: 24px;
        }
      }
    }
    &.is-close {
      width: auto;
      .stream-chat-header {
        .heading {
          display: none;
        }
        .chat-icon {
          display: none;
        }
      }
      .stream-chat-body {
        display: none;
      }
      .stream-chat-footer {
        display: none;
      }
    }
  }
  .tab-section {
    padding: 15px 0;
    .tab-section-title {
      font-size: 24px;
      margin: 0;
      color: $primary;
      font-family: $font-bold;
      text-transform: capitalize;
    }
    &.follower-section {
      .videos {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        padding: 15px 0 0;
        .video {
          width: calc(33.333% - 10px);
          height: 200px;
          background: $white;
          margin: 5px;
          cursor: pointer;
          box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.21);
          border-radius: 10px;
          overflow: hidden;
        }
      }
    }
    .empty {
      padding: 25px 0;
      display: flex;
      justify-content: center;
      align-items: center;
      p {
        margin: 0;
        color: $black;
        font-family: $font-medium;
      }
    }
  }
}
