.about-page {
  padding: 50px 0;
  .about-us {
    h1 {
      font-size: 24px;
      font-family: $font-bold;
      color: $black;
      margin-bottom: 20px;
    }
    p {
      font-size: 16px;
      font-family: $font-regular;
      color: $black;
      margin: 0;
    }
    .about-nav {
      padding: 0;
      margin: 35px 0 20px;
      list-style: none;
      display: flex;
      li {
        a {
          padding: 12px 15px;
          border: none;
          background-color: $white;
          color: $black;
          border-radius: 10px;
          font-size: 14px;
          margin: 0 14px;
          box-shadow: 0px 12px 40px 0px rgba(0, 0, 0, 0.16);
          outline: none;
          min-width: 200px;
          text-align: center;
          &:first-child {
            margin-left: 0;
          }
          &.active {
            background-color: $primary;
            color: $white;
          }
        }
      }
    }
  }
  .card-section {
    display: flex;
    align-items: center;
    box-shadow: 0px 12px 40px 0px rgba(0, 0, 0, 0.16); 
    padding: 40px 25px;
    margin-bottom: 50px;
    .card-content {
      flex: 1;
      padding-right: 50px;

    }
    .card-img {
      width: 550px;
      img {
        width: 100%;
        height: 345px;
        border-radius: 10px;
        object-fit: cover
      }
    }
  }
  h1 {
    margin: 0 0 20px 0;
    color: $black;
    font-family: $font-bold;
    font-size: 24px;
    text-transform: uppercase;
  }
  h2 {
    margin: 0 0 20px 0;
    color: $black;
    font-family: $font-bold;
    font-size: 18px;
  }
  p {
    margin: 0 0 20px 0;
    color: $black;
    font-family: $font-regular;
  }
}
