.sidebar {
  width: 100%;
  height: 100%;
  background: $lightenGray;
  padding: 20px 15px;
  .channels-container {
    width: 100%;
    margin-bottom: 30px;
    .channels-header{
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-bottom: 15px;
      .heading {
        font-size: 14px;
        margin: 0 15px 0 0;
        transition: 0.3s;
      }
    }
    .channels-list {
      padding: 0 15px 0 0;
      margin: 0 -15px 0 0;
      list-style: none;
      height: 500px;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 15px;
        border-radius: 0;
      }
      &::-webkit-scrollbar-track {
        background: #cccccc;
        border-radius: 0;
      }
      &::-webkit-scrollbar-thumb {
        background: #6f6f6f;
        border-radius: 0;
      }
      &::-webkit-scrollbar-thumb:hover {
        background: #6f6f6f;
      }
      .channels-list-item {
        a {
          text-decoration: none;
          border-bottom: 1px solid #d8d6d7;
          display: flex;
          flex-direction: row;
          align-items: center;
          width: 100%;
          padding: 10px 10px 10px 15px;
          border-left: 1px solid transparent;
          transition: 0.3s;
          &.active {
            background: $white;
            border-left-color: $primary;
          }
          &:hover {
            background: $white;
            border-left-color: $primary;
            cursor: pointer;
          }
          .channels-list-item-image {
            width: 62px;
            height: 62px;
            border-radius: 62px;
            object-fit: cover;
            margin-right: 15px;
          }
          .channels-list-item-content {
            width: calc(100% - 126px);
            h6 {
              margin: 0 0 5px;
              font-size: 12px;
              font-family: $font-bold;
              color: $black;
              width: 100%;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: nowrap;
            }
            p {
              font-size: 12px;
              margin: 0;
              font-family: $font-regular;
              color: $gray;
              width: 100%;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: nowrap;
            }
          }
          .status {
            width: 8px;
            height: 8px;
            background: $primary;
            border-radius: 100px;
            margin: 0 10px;
          }
          .viewers-text {
            font-size: 13px;
            color: $gray;
          }
        }
      }
    }
  }
  &.isOpen {
    .channels-container {
      .channels-header {
        justify-content: center;
        .heading {
          display: none;
        }
      }
      .channels-list {
        margin-right: 0;
        padding-right: 0;
        height: auto;
        .channels-list-item {
          a{
            background: transparent !important;
            border-color: transparent;
            border-left-color: transparent !important;
            padding-right: 0;
            padding-left: 0;
            .channels-list-item-image {
              margin: 0;
            }
            .channels-list-item-content {
              display: none;
            }
            .status {
              display: none;
            }
            .viewers-text {
              display: none;
            }
          }
        }
      }
    }
    .ads-container {
      display: none;
    }
  }
}