.header {
  box-shadow: 0 5px 9px 0 rgba(0, 0, 0, 0.11);
  .navbar {
    background: $offWhite;
    padding: 10px 25px;
    width: 100%;
    .navbar-brand{
      margin: 0;
      padding: 0;
      .logo {
        width: 70px;
        height: 66px;
        object-fit: contain;
      }
    }
    .navbar-nav{
      margin: 0 15px;
      .nav-link {
        font-size: 14px;
        color: $black !important;
        font-family: $font-medium;
        margin: 0 15px;
        padding: 0;
        svg {
          display: none;
        }
      }
      .dropdown {
        margin: 0 15px;
        &.select-language {
          margin: 0 25px;
          display: flex;
          align-items: center;
          svg {
            margin-right: 5px;
          }
          .down-arrow {
            margin-right: 0;
            //margin-left: 5px;
          }
          select {
            background: transparent;
            border: none;
            outline: none;
            color: $black;
            font-size: 14px;
            font-family: $font-medium;
            width: 125px;
            white-space: normal;
            -moz-appearance:none; /* Firefox */
            -webkit-appearance:none; /* Safari and Chrome */
            appearance:none;
            option {
              font-size: 14px;
              font-family: $font-medium;
              color: $black;
            }
          }
          //button {
          //  background: transparent;
          //  border-radius: 0;
          //  box-shadow: none;
          //  color: $black;
          //  display: flex;
          //  justify-content: center;
          //  align-items: center;
          //  padding: 0 10px;
          //
          //  &:after {
          //    content: '';
          //    border: solid black;
          //    border-width: 0 2px 2px 0;
          //    display: inline-block;
          //    padding: 3px;
          //    transform: rotate(45deg);
          //    -webkit-transform: rotate(45deg);
          //    margin-left: 10px;
          //  }
          //}
          //.dropdown-menu {
          //  min-width: 140px;
          //  left: 0;
          //  top: 42px;
          //}
        }
        &.setting-dropdown {
          margin: 0;
          .dropdown-toggle {
              width: 46px;
              height: 46px;
            img {
              width: 18px;
              height: 22px;
              object-fit: contain;
            }
          }
          .dropdown-menu {
            right: 15px;
            left: auto;
          }
        }
        .dropdown-toggle {
          background: $whisper;
          border: none;
          border-radius: 50%;
          box-shadow: 0 5px 24px 0 rgba(0, 0, 0, 0.16);
          padding: 5px;
          &.userAvatar{
            width: 46px;
            height: 46px;
            .userAvatarImage {
              width: 18px;
              height: 22px;
              object-fit: contain;
            }
          }
          img{
            width: 36px;
            height: 36px;
          }
          &:after {
            display: none;
          }
        }
        .dropdown-menu {
          box-shadow: 0 5px 21px 0 rgba(0, 0, 0, 0.21);
          overflow: hidden;
          margin: 0;
          padding: 10px 0;
          border: none;
          border-radius: 10px;
          min-width: 200px;
          left: 25px;
          top: 52px;
          &.show{
            display: flex;
          }
          flex-direction: column;
          a {
            padding: 15px 23px;
            font-size: 12px;
            font-family: $font-medium;
            color: $black;
            background: transparent !important;
            transition: 0.3s;
            text-decoration: none;
            width: 100%;
            display: flex;
            align-items: center;
            svg {
              color: $black;
              font-size: 18px;
              margin-right: 8px;
              transition: 0.3s;
            }
            .custom-switch {
              padding: 0;
              .custom-control-input {
                left: auto;
                right: 0;
              }
              .custom-control-label{
                vertical-align: sub;
                padding-right: 10px;
                padding-top: 2px;
                &:before {
                  right: -2.25rem;
                  left: auto;
                }
                &:after {
                  right: calc(-1.55rem + 3px);
                  left: auto;
                }
              }
            }
            .custom-switch .custom-control-input:checked~.custom-control-label::after {
              transform: translateX(.75rem);
            }
            .custom-control-input:checked~.custom-control-label::before {
              border-color: $primary;
              background-color: $primary;
            }
            &:hover {
              color: $primary;
              svg {
                color: $primary;
              }
            }
          }
        }
      }
      .normal-button {
        padding: 12px 30px;
        font-size: 12px;
        margin-right: 20px;
        min-width: 120px;
      }
    }
    .header-search-container{
      flex: 1;
      position: relative;
      margin: 0 0 0 30px;
      input {
        width: 100%;
        margin: 0;
        border: none;
        border-radius: 100px;
        height: 50px;
        padding: 8px 35px;
        color: $black;
        font-family: $font-medium;
        font-size: 14px;
        box-shadow: 0 5px 21px 0 rgba(0, 0, 0, 0.21);
        &::placeholder{
          color: $black;
        }
      }
      button{
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        background: $whisper;
        outline: none !important;
        border: none !important;
        border-radius: 100px;
        padding: 12px 22px;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: 0 5px 21px 0 rgba(0, 0, 0, 0.1);
        svg{
          font-size: 22px;
          color: $black;
        }
      }
    }
  }
  .google {
    .goog-te-gadget {
      font-size: 0;
    }
     span {
       display: none;
     }
    .goog-te-combo {
      display: flex;
    }
  }
}
