body {
  margin: 0;
}

.normal-button {
  background: $primary;
  color: $white;
  border-color: $primary;
  outline-color: $primary;
  font-size: 14px;
  font-family: $font-bold;
  padding: 15px 30px;
  border-radius: 100px;
  box-shadow: none !important;
  &:disabled {
    background: $primary !important;
    border-color: $primary !important;
  }
  &:focus{
    background: $primary !important;
    border-color: $primary !important;
  }
  &:active{
    background: $primary !important;
    border-color: $primary !important;
  }
  &:hover{
    background: $primary !important;
    border-color: $primary !important;
  }
}
.black-button {
  background: $white !important;
  color: $black !important;
  border-color: $black !important;
  &:focus{
    background: $white !important;
    border-color: $black !important;
  }
  &:active{
    background: $white !important;
    border-color: $black !important;
  }
  &:hover{
    background: $white !important;
    border-color: $black !important;
  }
}
.heading {
  font-size: 24px;
  color: $black;
  font-family: $font-bold;
  width: 100%;
  margin-bottom: 15px;
  text-transform: uppercase;
  span {
    color: $primary;
  }
}
.main-wrapper {
  padding: 0 20px;
  width: 100%;
  height:100%;
  display: flex;
  flex-direction: column;
}
.section {
  padding: 25px 0;
  &.categories-section{
    .slick-arrow {
      background-position: 0 27px;
    }
  }
  &.slider-section{
    .slick-slider {
      padding: 0 35px;
      .slick-arrow {
        width: 26px;
        height: 27px;
        background-image: url("../images/home-imges-all/up-arrow.png");
        background-size: cover;
        z-index: 1;
        transition: 0.3s;
        &.slick-prev{
          left: 5px;
        }
        &.slick-next{
          right: 5px;
          transform: rotateZ(180deg);
          margin-top: -18px;
        }
        &:hover {
          background-position: 0 0;
        }
        &:before {
          display: none;
        }
        &:after {
          display: none;
        }
      }
    };

  }
  .section-button-container {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 38px;
  }
}
.c-card {
  margin: 2px 10px;
  .c-card-header {
    height: 225px;
    position: relative;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.21);
    .c-card-header-image {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .tag {
      position: absolute;
      background: $primary;
      color: $white;
      font-size: 12px;
      font-family: $font-medium;
      padding: 2px 20px;
      border-radius: 100px;
      &.is-live{
        top: 20px;
        right: 20px;
      }
      &.viewers{
        bottom: 20px;
        left: 20px;
      }
    }
  }
  .c-card-body {
    background: $whisper;
    padding: 20px 15px;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.16);
    margin: -5px 4px 0;
    display: flex;
    flex-direction: row;
    .c-card-user-image {
      width: 52px;
      height: 52px;
      border-radius: 100%;
      box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.16);
      object-fit: cover;
    }
    .c-card-body-content {
      margin-left: 15px;
      h5 {
        margin: 5px 0 10px 0;
        font-size: 14px;
        font-family: $font-bold;
        color: $black;
      }
      p {
        margin: 0;
        font-size: 14px;
        font-family: $font-regular;
      }
    }
  }
}
.open-drawer {
  .contain-with-sidebar {
    .sidebar-container {
      width: auto;
    }
    .contain-container {
      width: calc(100% - 93px);
      .main-wrapper {
        .header-section {
          .header-section-slider {
            .header-section-slide {
              padding: 0 60px;
            }
            .slick-arrow {
              background-position: 0 0;
              &.slick-prev{
                left: 5px;
              }
              &.slick-next{
                right: 5px;
              }
            }
          }
        }
      }
    }
  }
}
.contain-with-sidebar {
  display: flex;
  flex-direction: row;
  .sidebar-container {
    width: 300px;
  }
  .contain-container {
    width: calc(100% - 300px);
  }
}
.circle-button {
  background: red;
  border: none;
  border-radius: 100px;
  width: 44px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: $whisper;
  box-shadow: 0 5px 24px 0 rgba(0, 0, 0, 0.16);
  outline: none !important;
  svg{
    font-size: 24px;
  }
}
.page-title {
  margin: 40px 0 50px;
  font-family: $font-bold;
  font-size: 24px;
  color: $black;
  text-transform: uppercase;
}
.ad-container {
  width: 100%;
  height: 140px;
  margin-bottom: 15px;
  &.full{
    height: 390px;
  }
  .ad-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.page {
  padding: 0 50px;
  width: 100%;
}

.tab-nav {
  display: flex;
  flex-direction: row;
  padding: 0;
  margin: 15px 0;
  list-style: none;
  flex: 1;
  li {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 20px;
    a {
      padding: 0;
      color: $black;
      font-size: 14px;
      font-family: $font-regular;
      border-bottom: 3px solid transparent;
      &.active {
        color: $primary;
        font-family: $font-medium;
        border-bottom-color: $primary;
      }
    }
    &:last-child {
      margin-right: 0;
    }
  }
}

.not-found {
  padding: 200px 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  h6 {
    font-size: 28px;
    margin: 0 0 15px 0;
    text-transform: uppercase;
    font-family: $font-black;
  }
  p {
    margin: 0;
    font-size: 28px;
    text-transform: uppercase;
    font-family: $font-black;
  }
}
.hide-input-icon {
    input {
        background-image: none !important;
    }
}

.normal-content {
  h4 {
    font-size: 24px;
    margin: 0 0 15px;
    text-transform: uppercase;
    font-family: $font-black;
  }
  p {
    margin: 0 0 15px 0;
    font-size: 16px;
    font-family: $font-regular;
    line-height: 24px;
  }
}
.forget-button {
  display: flex;
  justify-content: flex-end;
  button {
    font-size: 14px;
    color: black;
    font-family: $font-semiBold;
    cursor: pointer;
    padding: 0;
    background: transparent;
    border: none;
  }
}
.loading-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.95);
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
  .spinner-grow {
    width: 4rem;
    height: 4rem;
  }
}
.error-container {
  padding: 15px 0 40px;
  p {
    margin: 0;
    text-align: center;
    font-size: 16px;
    font-family: $font-medium;
  }
}