@media (min-width: 320px) and (max-width: 920px) {
  .responsive-web {
    .channel-page {
      .inner-container {
        width: 100%;
        .channel-page-header {
          .user-detail {
            width: 100%;
          }
          ul {
            flex-wrap: wrap;
            margin: 10px 0 15px;
            li {
              margin: 0 10px;
              a {
                font-size: 12px;
              }
              button {
                font-size: 12px;
                padding: 6px 20px;
                height: 35px;
              }
              &:first-child {
                margin-left: 0;
              }
              &:last-child {
                margin-right: 0;
              }
            }
          }
        }
        .video-container {
          margin: 5px 0 15px;
          height: 350px;
        }
        .channel-detail-container {
          .channel-detail-header {
            margin-bottom: 15px;
            h4 {
              font-size: 16px;
            }
            .users-and-viewers {
              margin-left: 15px;
            }
          }
        }
      }
      .stream-chat-container {
        display: none;
      }
    }
  }
}
@media (min-width: 320px) and (max-width: 1200px) {
  .responsive-web {
    .header {
      .navbar {
        padding: 10px 15px;
        display: flex;
        flex-direction: row;
        .navbar-brand{
          .logo {
            width: 50px;
            height: 46px;
          }
        }
        .navbar-toggler {
          border: none;
        }
        .navbar {
          padding: 0;
          flex: 1;
          justify-content: flex-end;
          .navbar-nav {
            margin: 0 10px;
            .normal-button {
              padding: 8px 15px;
              font-size: 12px;
              margin-right: 15px;
              min-width: 82px;
            }
            .dropdown {
              &.select-language {
                display: none;
                margin: 0 15px;
              }
              .dropdown-toggle {
                img {
                  width: 26px;
                  height: 26px;
                }
                &.userAvatar {
                  width: 36px;
                  height: 36px;
                  .userAvatarImage {
                    width: 16px;
                    height: 20px;
                  }
                }
              }
            }
            .nav-link {
              span {
                display: none;
              }
              svg {
                width: 20px;
                height: 20px;
                display: flex;
              }
            }
          }
          .header-search-container {
            display: none;
          }
          &:last-child {
            //display: none;
          }
        }
      }
    }
    .contain-with-sidebar {
      .sidebar-container {
        width: auto;
        .sidebar {
          padding: 15px;
          .channels-container {
            .channels-header {
              display: none;
            }
            .channels-list {
              height: auto;
              .channels-list-item {
                a {
                  padding: 10px 0;
                  .channels-list-item-image {
                    margin: 0;
                    width: 38px;
                    height: 38px;
                  }
                  .channels-list-item-content {
                    display: none;
                  }
                  .status {
                    display: none;
                  }
                  .viewers-text {
                    display: none;
                  }
                  &:hover {
                    background: transparent;
                    border-left-color: transparent;
                    cursor: pointer;
                  }
                }
              }
            }
          }
          .ads-container {
            display: none;
          }
        }
      }
      .contain-container {
        width: calc(100% - 69px);
      }
    }
    .main-wrapper {
      padding: 0 15px;
      .header-section {
        margin-top: 10px;
        .header-section-slider {
          height: 300px;
          .header-section-slide {
            height: 300px;
          }
          .slick-arrow{
            &.slick-next {
              right: 15px;
            }
            &.slick-prev {
              left: 15px;
            }
          }
        }
      }
    }
    .section {
      padding: 15px 0;
      &.categories-section {
        padding: 15px;
      }
      .section-button-container {
        margin-top: 25px;
      }
    }
    .heading {
      font-size: 16px;
    }
    .c-card {
      .c-card-header {
        height: 200px;
      }
      .c-card-body {
        padding: 15px 10px;
        .c-card-user-image {
          width: 42px;
          height: 42px;
        }
        .c-card-body-content {
          margin-left: 10px;
          h5 {
            font-size: 12px;
          }
          p {
            font-size: 12px;
          }
        }
      }
    }
    .normal-button {
      font-size: 12px;
      padding: 10px 25px;
    }
    .footer {
      padding: 10px;
      p {
        font-size: 12px;
      }
    }
    .about-page {
      padding: 25px 0;
      .about-us {
        h1 {
          font-size: 18px;
          margin-bottom: 15px;
        }
        p {
          font-size: 14px;
        }
        .about-nav {
          margin: 25px 0;
          li {
            a {
              min-width: auto;
            }
          }
        }
      }
      .card-section {
        padding: 25px 15px;
        margin-bottom: 30px;
        flex-direction: column;
        .card-content {
          padding: 0;
        }
        .card-img {
          width: 100%;
          img {
            height: 265px;
          }
        }
      }
      h1 {
        font-size: 18px;
      }
      h6 {
        font-size: 14px;
      }
      p {
        font-size: 14px;
      }
    }
  }
}

@media (min-width: 320px) and (max-width: 767px) {
  .responsive-web {
    .settings {
      .tab-section {
        .tab-section-title {
          font-size: 18px;
        }
        .tab-section-description {
          font-size: 12px;
        }
        .tab-section-body {
          padding: 25px 15px;
          .form-group {
            display: flex;
            flex-direction: column;
            &.upload-picture {
              flex-direction: row;
              .input-with-text {
                padding-left: 0;
              }
              .upload-picture-button {
                margin-bottom: 10px;
              }
            }
            .profile-picture {
              margin-right: 25px;
              img {
                width: 92px;
                height: 92px;
              }
            }
            .form-label {
              margin-bottom: 10px;
            }
          }
          .button-container {
            padding: 0;
          }
        }
      }
    }
    .page {
      padding: 0;
      .accordion {
        .accordion-container {
          margin: 0 0 15px;
          .accordion-header {
            button {
              font-size: 14px;
              padding: 15px;
            }
          }
          .accordion-body {
            padding: 15px;
            p {
              font-size: 12px;
            }
          }
        }
      }
    }
    .page-title {
      margin: 15px 0;
      font-size: 18px;
    }
    .privacy-section {
      padding: 15px 0;
      p {
        font-size: 14px;
      }
    }
  }
}
