.main-wrapper {
  .header-section {
    margin-top: 20px;
    .header-section-slider {
      height: 560px;
      border-radius: 10px;
      overflow: hidden;
      .header-section-slide {
        height: 560px;
        .header-section-slide-image {
          width: 100%;
          height: 100%;
          object-fit: cover;
          border-radius: 10px;
        }
      }
      .slick-arrow {
        width: 35px;
        height: 36px;
        background-image: url("../images/home-imges-all/up-arrow.png");
        background-position: 0 36px;
        z-index: 1;
        transition: 0.3s;
        &.slick-prev{
          left: 30px;
        }
        &.slick-next{
          right: 30px;
          transform: rotateZ(180deg);
          margin-top: -18px;
        }
        &:hover {
          background-position: 0 0;
        }
        &:before {
          display: none;
        }
        &:after {
          display: none;
        }
      }
    }
  }
  .categories-section {
    background: $primary;
    padding: 20px 20px 30px;
    .heading {
      color: $white;
      span {
        color: $white;
      }
    }
  }
}