.settings {
  height: 100%;
  padding: 25px 0;
  .tab-nav {
    li {
      &:first-child {
        margin-left: 0;
      }
    }
  }
  .tab-section {
    padding: 20px 0;
    width: 100%;
    .tab-section-title {
      margin: 0 0 5px 0;
      font-family: $font-bold;
      font-size: 24px;
      color: $primary;
    }
    .tab-section-description {
      margin: 0;
      font-family: $font-medium;
      font-size: 14px;
      color: $black;
    }
    .tab-section-body {
      margin-top: 25px;
      width: 100%;
      padding: 25px;
      background: $offWhite;
      box-shadow: 0 5px 9px 0 rgba(0, 0, 0, 0.11);
      border-radius: 10px;
      .form-group {
        display: flex;
        margin-bottom: 25px;
        .form-label {
          margin: 0;
          min-width: 20%;
          text-transform: capitalize;
          font-family: $font-bold;
          font-size: 14px;
        }
        .profile-picture {
          min-width: 20%;
          img {
            width: 122px;
            height: 122px;
            border-radius: 100%;
            object-fit: cover;
          }
        }
        .input-with-text {
          flex: 1;
          input {
            margin-bottom: 10px;
            font-family: $font-regular;
            font-size: 14px;
            color: $black;
            border-radius: 5px;
            border-color: transparent;
            height: 42px;
            padding: 5px 20px;
            box-shadow: 0 5px 9px 0 rgba(0, 0, 0, 0.11);
            &:focus {
              border-color: #f12b2d;
              box-shadow: 0 0 0 0.2rem rgba(239, 43, 45, 0.25);
            }
          }
          textarea {
            margin-bottom: 10px;
            font-family: $font-regular;
            font-size: 14px;
            color: $black;
            border-radius: 5px;
            border-color: transparent;
            padding: 15px 20px;
            box-shadow: 0 5px 9px 0 rgba(0, 0, 0, 0.11);
            resize: none;
            &:focus {
              border-color: #f12b2d;
              box-shadow: 0 0 0 0.2rem rgba(239, 43, 45, 0.25);
            }
          }
          p {
            margin: 0;
            font-family: $font-medium;
            font-size: 12px;
          }
        }
        &.upload-picture {
          align-items: center;
          margin-bottom: 35px;
          .input-with-text {
            padding-left: 25px;
          }
          .upload-picture-button {
            margin-bottom: 15px;

            label {
              margin: 0;
              padding: 10px 35px;
              background: $primary;
              color: $white;
              border-radius: 25px;
              font-size: 14px;
              font-family: $font-medium;
            }
            input {
              display: none;
            }
          }
        }
      }
      .button-container {
        padding: 15px 15px 0;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
}