.page {
  .accordion {
    .accordion-container {
      margin: 0 0 20px;
      border-radius: 10px;
      overflow: hidden;
      .accordion-header {
        background: $primary;
        display: flex;
        button {
          background: transparent;
          border: none;
          font-size: 16px;
          color: $white;
          font-family: $font-bold;
          padding: 15px 30px;
          flex: 1;
          text-align: left;
          outline: none;
          svg {
            float: right;
            font-size: 24px;
          }
        }
      }
      .accordion-body {
        padding: 30px;
        background: $lightenGray;
        p {
          font-family: $font-regular;
          color: $black;
          font-size: 16px;
        }
      }
    }
  }
}